
import Vue from 'vue'
import { getProductDetail, submitQuestion} from '@/api/detail.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: 'questions',
components: {
Tab,
TabItem,
},
data () {
return {
content:'',
productid:'',
skuid:'',
orderDetail:''
}
},
methods: {
questionsubmit(){
if(this.content==''){
this.$vux.toast.text('请输入您想询问的问题', 'middle')
return
}
const postData = {
productid:this.productid,
content:this.content
}
submitQuestion(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text("发布成功", 'middle')
this.$router.push({path: '/orderDetail', query: {'productid': this.productid,'skuid': this.skuid}})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
gotodetail(){
this.$router.push({path: '/orderDetail', query: {'productid': this.productid,'skuid': this.skuid}})
}
},
created () {
this.productid = this.$route.query.productid
this.skuid = this.$route.query.skuid
const postData = {
productid:this.productid,
skuid:this.skuid
}
getProductDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.orderDetail = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
mounted(){
const that = this
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: "EDIFIER问答",
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/issue?productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: "EDIFIER问答",
desc: '',
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/issue?productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



